html,body{margin:0;padding:0}body{font-size:16px;font-family:"Hind", sans-serif;line-height:1.75rem}.sticky{position:-webkit-sticky;position:sticky;top:20px}.App-theme--light{background-color:#f4f4f4;color:#333}.App-theme--light .Card{color:#4d4d4d;background-color:#fff}.App-theme--light .Card__name{color:#333}.App-theme--light .Card__tag{background-color:#e6e6e6}.App-theme--light .SocialNetwork__link{color:#4d4d4d}.App-theme--dark{background-color:#333;color:#fff}.App-theme--dark .Card{color:#b3b3b3;background-color:#212121}.App-theme--dark .Card__name{color:#fff}.App-theme--dark .Card__tag{background-color:#333}.App-theme--dark .SocialNetwork__link{color:#b3b3b3}.Profile{padding:2rem;text-align:center}.Profile__name{margin-bottom:0;font-family:"Oswald", sans-serif;font-weight:500;text-transform:uppercase}.Profile__description{font-size:20px;line-height:2rem}.Thumbnail{object-fit:cover;overflow:hidden;margin:auto}.CardList{margin:1rem;padding:0;list-style:none;width:calc(100% - 2rem)}.CardList__item{width:calc((100% / 3) - 2rem);margin:1rem}@media (max-width: 768px){.CardList__item{width:100%}}.Card{box-shadow:1px 1px 1px rgba(0,0,0,0.05),5px 5px 5px rgba(0,0,0,0.05);border-radius:4px;overflow:hidden;background-color:#fff;display:block;text-decoration:none;color:#333;transition:all 0.3s ease-in-out}.Card:hover{box-shadow:1px 1px 1px rgba(0,0,0,0.05),5px 5px 10px rgba(0,0,0,0.2);transform:scale(1.05)}.Card__body{padding:1.5rem}.Card__link{width:100%;display:block;text-align:right}.Card__header img{width:100%}.Card__name{margin-top:0;font-family:"Oswald", sans-serif;font-weight:500;text-transform:uppercase}.Card__tags{width:100%;display:flex;flex-wrap:wrap}.Card__tag{padding:0.25rem 0.75rem;border-radius:4px;margin:0.5rem 0.5rem 0 0;font-size:12px;text-transform:uppercase}.SocialNetwork{padding:0;margin:0;text-align:center}.SocialNetwork__item{display:inline-block;padding:0 1rem}.SocialNetwork__link{font-size:1.5rem}.SocialNetwork__link>div{transition:all 0.3s ease-in-out}.SocialNetwork__link>div:hover{transform:scale(1.25)}

